// Component: Reservation Box

.reservation-box {
	.date {
		margin-bottom: $gap-size-20;
		text-align: center;
		.inp-date {
			margin: 0 10px;
			width: 70%;
			max-width: 180px; } }
	.slot-wrap {
		.times {
			width: 100%;
			padding-left: 10px;
			padding-right: 10px;
			td {
				padding: 0;
				width: calc(100%/12); } }
		.slots {
			display: flex;
			justify-content: space-around;
			background-color: #FAFAFA;
			padding: 10px 15px;
			border-radius: 25px;
			.slot {
				display: inline-block;
				width: 28px;
				height: 28px;
				margin-right: 12px;
				background-color: $color-green;
				border-radius: 50%;
				cursor: pointer;
				text-align: center;
				color: transparent;
				user-select: none;
				transition: box-shadow .1s;
				&:last-child {
					margin-right: 0; }
				&:not(.unavailable):hover {
					box-shadow: $shadow-small-hover; }
				&.unavailable {
					background-color: $color-red;
					cursor: default; }
				&.selected {
					background-color: $color-primary;
					box-shadow: $shadow-normal; } } } } }
@media screen and (max-width: 767px) {
	.reservation-box {
		.slot-wrap {
			table.times {
				td {
					display: table-cell; } } } } }
@media screen and (max-width: 579px) {
	.reservation-box {
		.slot-wrap-wrapper {
			overflow-x: auto;
			white-space: nowrap; }
		.slot-wrap {
			display: inline-block;
			table.times {
				display: none; }
			.slots {
				background-color: transparent;
				padding-left: 0;
				padding-right: 0;
				.slot {
					color: #FFF;
					width: 50px;
					height: 50px;
					padding-top: 1em; } }
			.slots#slots-2 {
				padding-left: 12px; } } } }
