// import fonts here
@font-face {
	font-family: "Noto Sans Thai UI";
	src: url('../fonts/notosansthaiui-r.woff2') format('woff2'),
	url('../fonts/notosansthaiui-r.woff') format('woff') {}
	font-weight: 400;
	font-style: normal;
	font-display: swap; }
@font-face {
	font-family: "Noto Sans Thai UI";
	src: url('../fonts/notosansthaiui-b.woff2') format('woff2'),
	url('../fonts/notosansthaiui-b.woff') format('woff') {}
	font-weight: 700;
	font-style: normal;
	font-display: swap; }
@font-face {
	font-family: 'Ubuntu';
	src: url('../fonts/ubuntu-r.woff2') format('woff2'),
	url('../fonts/ubuntu-r.woff') format('woff') {}
	font-weight: 400;
	font-style: normal;
	font-display: swap; }
@font-face {
	font-family: 'Ubuntu';
	src: url('../fonts/ubuntu-b.woff2') format('woff2'),
	url('../fonts/ubuntu-b.woff') format('woff') {}
	font-weight: 700;
	font-style: normal;
	font-display: swap; }
@font-face {
	font-family: 'Font Awesome 5 Free';
	font-style: normal;
	font-weight: 900;
	src: url("../fonts/fa-solid-900.eot");
	src: url("../fonts/fa-solid-900.eot?#iefix") format("embedded-opentype"),
	url("../fonts/fa-solid-900.woff2") format("woff2"),
	url("../fonts/fa-solid-900.woff") format("woff"),
	url("../fonts/fa-solid-900.ttf") format("truetype"),
	url("../fonts/fa-solid-900.svg#fontawesome") format("svg") {}
	font-display: block; }

.fa, .fas {
  font-family: 'Font Awesome 5 Free';
  font-weight: 900; }

h1, h2, h3, h4, h5, h6 {
	font-family: $font-styled; }
h1 {
	font-size: 3em; }
h2 {
	font-size: 2.8em; }

.page-title-wrap {
	margin-bottom: 3em;
	.page-title {
		margin: 0; }
	.page-subtitle {
		margin-top: 0.6em;
		font-size: 1.2em;
		font-weight: 400;
		color: $color-grey; }
	.page-pretitle {
		margin-top: 0;
		margin-bottom: 0.3em;
		font-size: 1.2em;
		font-weight: 400;
		color: $color-grey; } }

.text-center {
	text-align: center; }
